import { AppModule } from '../../app.module';

export class SafeMsgBuilder {
    private msg: string;
    private doc: any = AppModule.doc;
    private type: string;

    constructor() {
        this.init();
    }

    init() {
        this.msg = ""+this.type+" ";
    }

    setGender(gender) {
        this.type = gender == 'female'? '' : '';
        this.init();
        return this;
    }

    setName(name) {
        this.msg += name;
        return this;
    }

    setCode(code) {
        this.msg += " ";
        this.msg += " "+this.type+" " + code;
        return this;
    }

    setLevel(level) {
        this.msg += " ";
        this.msg += " "+this.type+"  " + level;
        return this;
    }

    setDivision(division) {
        this.msg += " ";
        this.msg += "  " + division;
        return this;
    }

    setOldBalance(value) {
        this.msg += " ";
        this.msg += " "+this.type+"     " + value + "  ";
        return this;
    }
    
    setCurrentBalance(value) {
        this.msg += " ";
        this.msg += "   " + value + "  ";
        return this;
    }

    setPaidValue(value) {
        this.msg += " ";
        this.msg += "     " + value + "  ";
        return this;
    }

    say() {
        this.doc.say(this.msg);
    }

}
